import { Button, Typography } from '@mui/material'
import React from 'react'
import Box from '@mui/material/Box';
import Timer from '../Components/Timer'

const Exam = () => {
  return (
    <>

        <Box>
          <Button>Start test</Button>
        </Box>


        <Timer duration={1*60*60*1000}/>
    </>
  )
}

export default Exam